// tslint:disable-next-line:max-line-length
import { Component, ViewChild, Input, OnChanges, Output, EventEmitter, SimpleChanges, ComponentFactoryResolver, Injector } from '@angular/core';
import { MetadataRTService } from '../../metadatart.service';
import { Metadata4RefDto, MetadataPackageHeader } from '../../../metadata4refdto';
import { BsModalService } from '@farris/ui-modal';
import {NotifyService} from '@farris/ui-notify';
import {TranslateService} from '@ngx-translate/core';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-select-metadatart',
  templateUrl: './selectmetadatart.component.html',
  styleUrls: ['./selectmetadatart.component.css']
})
export class SelectMetadataRTComponent implements OnChanges {
  @Input() typeList: string;
  @Input() boId: string;
  @Input() bizTypeId: string;
  classifier: string;
  @Output() closeModalEmitter: EventEmitter<any>;
  @Output() selectedMetadata = new EventEmitter<any>();
  public confirm='ç¡®å®';
  public cancel='åæ¶';

  private notifyService:NotifyService;

  constructor(private metadataService: MetadataRTService, 
              private modalService: BsModalService,
              private resolver: ComponentFactoryResolver, private injector: Injector) {
    this.closeModalEmitter = new EventEmitter<any>();
    this.notifyService = this.injector.get(NotifyService, null);
    if(localStorage.getItem('languageCode')==='en'){
      this.confirm='OK';
      this.cancel='Cancel';
    }
  }

  ngOnChanges(changes: SimpleChanges): void {

  }

  LoadMetadata() {
    if(this.metadataService.metadatas==null || this.metadataService.metadatas.length<=0){
      if (this.notifyService) {
        this.notifyService.warning("è¯·åéä¸­åæ°æ®ã");
        return;
      } else {
        console.log("è¯·åéä¸­åæ°æ®ã");
        return;
      }
    }
      const Id = this.metadataService.metadatas[0].ID;
      this.metadataService.GetMetadataRT(Id).subscribe(templates => {
        const metadata4Ref = new Metadata4RefDto();
        metadata4Ref.metadata = templates;
        metadata4Ref.packageHeader = new MetadataPackageHeader();
        this.selectedMetadata.emit(metadata4Ref);
        this.hideModal();
      });
      // æ¸ç©ºç¼å­
      this.metadataService.clearMetadataRT();   
  }

  hideModal(): void {
    this.closeModalEmitter.emit();
  }
  onSelectedChange(classifier: string) {
    this.classifier = classifier;
  }
}
