import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { GSPMetadataRTService } from '../metadata.rtservice';

@Injectable()
export class MetadataRTService {

  constructor(private gspMetadataService: GSPMetadataRTService) {
  }

  metadatas = [];
  addMetadataRT(metadata) {
    this.metadatas.push(metadata);
  }
  clearMetadataRT() {
    this.metadatas = [];
  }

  // æ ¹æ®ç±»åè·åCloud Serverä¸ææçåæ°æ®
  GetMetadataRTList(typeList: string): Observable<any> {
    return this.gspMetadataService.GetMetadataRTList(typeList);
  }

  GetMetadataRTListForHelp(typeList: string): Observable<any> {
    return this.gspMetadataService.GetMetadataRTListForHelp(typeList);
  }

  public getMetadataListByBizTypeId(bizTypeId: string, metadataTypes: string): Observable<any> {
    return this.gspMetadataService.getMetadataListByBizTypeId(bizTypeId, metadataTypes);
  }
  public getMetadataListByBizTypeIdForHelp(boList:string,bizTypeId: string, metadataTypes: string): Observable<any> {
    if(boList===undefined){
      boList='';
    }
    if(bizTypeId===undefined){
      bizTypeId='';
    }
    return this.gspMetadataService.getMetadataListByBizTypeIdForHelp(boList,bizTypeId, metadataTypes);
  }
  
  // è·æ ¹æ®Idåæå®åæ°æ®
  GetMetadataRT(metadataID: string): Observable<any> {
    return this.gspMetadataService.GetMetadataRT(metadataID);
  }
}
