import { Component, Input,  OnInit } from '@angular/core';
import { MetadataRTService } from '../../metadatart.service';
import { MetadataBaseInfo } from '../../metadatabaseinfo';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { process, State } from '@progress/kendo-data-query';
import { GridDataResult, DataStateChangeEvent } from '@progress/kendo-angular-grid';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-metadatalist',
  templateUrl: './metadatartlist.component.html',
  styleUrls: ['./metadatartlist.component.css']
})
export class TemplatemetadataRTComponent implements OnInit {
  // åç±»
  @Input() typeList: string;
  @Input() boId: string;
  @Input() bizTypeId: string;
  data: MetadataBaseInfo[];
  clear() {
    this.data = [];
  }
  public nameSpace='å½åç©ºé´';
  public name='åç§°';
  public fileName='æä»¶å';
  public type='ç±»å';
  public businessObject='ä¸å¡å¯¹è±¡';
  // tslint:disable-next-line:member-ordering
  public state: State = {
    skip: 0,
    take: 50,
    // Initial filter descriptor
    filter: {
      logic: 'and',
      filters: []
    }
  };

  // tslint:disable-next-line:member-ordering
  public gridData: GridDataResult;
  public dataStateChange(state: DataStateChangeEvent): void {
    this.state = state;
    this.gridData = process(this.data, this.state);
  }

  // tslint:disable-next-line:member-ordering
  selectableSetting: SelectableSettings = { mode: 'single', checkboxOnly: false };
  constructor(private metadataService: MetadataRTService) { }

  ngOnInit() {
    if(localStorage.getItem('languageCode')==='en'){
      this.nameSpace='NameSpace';
      this.name='Name';
      this.fileName='FileName';
      this.type='Type';
      this.businessObject='BusinessObject';
    }
    const metadatadto = [];
    if(this.boId||this.bizTypeId){
      this.metadataService.getMetadataListByBizTypeIdForHelp(this.boId,this.bizTypeId, this.typeList).subscribe(templates => {
        if (templates) { 
            templates.forEach(item => {
                metadatadto.push(item.metadata);
            });
        }
        this.data = metadatadto;
        this.gridData = process(this.data, this.state);
      });
    }else{
        this.metadataService.GetMetadataRTListForHelp(this.typeList).subscribe(templates => {
          if (templates) { 
              templates.forEach(item => {
                  metadatadto.push(item.metadata);
              });
          }
          this.data = metadatadto;
          this.gridData = process(this.data, this.state);
        });
      }
  }
  // éä¸­åæ°æ®åçäºä»¶å¤ç
  onSelectionChange(event) {
    if (event.selectedRows.length > 0) {
      this.metadataService.clearMetadataRT();
      const metadata = {
        ID: event.selectedRows[0].dataItem.id,
        RelativePath: event.selectedRows[0].dataItem.relativePath,
        FileName: event.selectedRows[0].dataItem.fileName
      };
      this.metadataService.addMetadataRT(metadata);
    }
  }
}
