import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class GSPMetadataRTService {
  // ç¯å¢åé
  // tslint:disable-next-line:variable-name
  private Server_IP: string;
  // tslint:disable-next-line:variable-name
  private MetadataRT_Base_Path = '/api/runtime/lcm/v1.0/rt-metadatas';
  // tslint:disable-next-line:variable-name
  private MetadataBizType_Base_Path = '/api/runtime/lcm/v1.0/metadata-biztypes';
  constructor(private httpClient: HttpClient, @Inject('url') private url: string) {
    this.Server_IP = (url === null || url === undefined) ? 'http://10.24.19.140:5000' : url;
  }

  /**
   * åæ°æ®è¿è¡æ¶æå¡
   */

  public GetMetadataRT(metadataID: string): Observable<any> {
    const url = this.Server_IP + this.MetadataRT_Base_Path + '/' + metadataID;
    return this.httpClient.get(url);
  }

  public GetMetadataRTList(typeList: string): Observable<any> {
    const url = this.Server_IP + this.MetadataRT_Base_Path + '?metadataTypes=' + typeList;
    return this.httpClient.get(url);
  }

  GetMetadataRTListForHelp(typeList: string): Observable<any> {
    const url = this.Server_IP + this.MetadataRT_Base_Path + '/helper?metadataTypes=' + typeList;
    return this.httpClient.get(url);
  }


  public getMetadataListByBizTypeId( bizTypeId: string, metadataTypes: string): Observable<any> {
    const url = this.Server_IP + this.MetadataBizType_Base_Path + '?bizTypeId=' + bizTypeId + '&metadataTypes=' + metadataTypes;
    return this.httpClient.get(url);
  }

  public getMetadataListByBizTypeIdForHelp(boList:string,bizTypeId: string, metadataTypes: string): Observable<any> {
    const url = this.Server_IP + this.MetadataBizType_Base_Path + '/helper?boList='+boList+'&bizTypeId=' + bizTypeId + '&metadataTypes=' + metadataTypes;
    return this.httpClient.get(url);
  }

  // è·ååæ°æ®ç±»åååç¼ä¿¡æ¯
  GetMetadataTypeList(): Observable<any> {
    const url = this.Server_IP + this.MetadataRT_Base_Path + '/typeList';
    return this.httpClient.get(url);
  }

  // è·ååç¼
  GetPostfix(metadataType): Observable<any> {
    this.GetMetadataTypeList().subscribe(typeList => {
      typeList.forEach(type => {
        if (metadataType.toLowerCase() === type.typeCode.toLowerCase()) {
          return type.postfix;
        }
      });
    });
    return new Observable<any>();
  }

}


