import { NgModule, ModuleWithProviders, InjectionToken, Inject } from '@angular/core';
import { GSPMetadataRTService } from './metadata.rtservice';
import { HttpClient, HttpHandler, HttpHeaders, HttpClientModule } from '@angular/common/http';

export function factory(http: HttpClient, url: string) {
  return new GSPMetadataRTService(http, url);
}

@NgModule({
  imports: [
    HttpClientModule
  ],
  exports: [],
  providers: []
})
export class GSPMetadataRTServiceModule {
  static forRoot(url: string): ModuleWithProviders {
    return {
      ngModule: GSPMetadataRTServiceModule,
      providers: [
        {provide: 'url', useValue: url},
        {provide: GSPMetadataRTService, useFactory: factory, deps: [HttpClient, 'url']}
      ]
    };
  }
}
